	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"Job Wanted","date":"05 Dec 2006","slug":"/2006-12-05-job-wanted/","tags":"Y2006"},"html":"<p><a href=\"//www.oeskovic.com/\">Dave</a> is looking for work and <a href=\"http://www.oeskovic.com/?p=984\">wants to stay in Hamburg</a>.</p>\n<blockquote><p>\n  Anyone out there who knows of work in the Hamburg area for an American graphic/web designing, market managing, ex-firefighter please let me know. <a href=\"http://www.oeskovic.com/files/DOeskovic-resume.pdf\">Check out my resume here.</a>\n</p></blockquote>"},"next":{"fields":{"title":"Ronald Schill, please come home!","date":"03 Dec 2006","slug":"/2006-12-03-ronald-schill-please-come-home/"}},"previous":{"fields":{"title":"Mama Nikolaus","date":"06 Dec 2006","slug":"/2006-12-06-mama-nikolaus/"}}},{"node":{"fields":{"title":"Ronald Schill, please come home!","date":"03 Dec 2006","slug":"/2006-12-03-ronald-schill-please-come-home/","tags":"Y2006"},"html":"<p>It's almost like old times... Hamburg's ex-Interior Senator, Judge Merciless, and all-around bad guy Ronald Barnabas Schill is back in the news. According to <a href=\"http://www.spiegel.de/politik/deutschland/0,1518,452123,00.html\" title=\"Gesuchter Zeuge: Ex-Senator Schill zur Fahndung ausgeschrieben - Politik - SPIEGEL ONLINE - Nachrichten\">Spiegel Online</a>, he's wanted by the police! Only as a witness (Honest. We swear!), though, for an old political juvenile justice scandal. Ronnie's hanging out in Brazil, last we heard, so it's doubtful that he'll be showing up for any special committee hearings anytime soon. But it's the thought that counts.</p>\n<p>And it's that thought that reminds us of the good old days of politics in Hamburg. Sure, we have another jilted Senator who's <a href=\"http://de.wikipedia.org/wiki/HeimatHamburg\">started his own party</a>. And we have one-party government that's testing the edge of constitutionality. (<a href=\"http://www.mehr-demokratie-hamburg.de/\">Citizen's initiative?</a> We don't like it, we'll just abolish it.) But without the bad man on his side, Ole von Beust is, simply put, boring. (If Berlin's Klaus Wowereit is <a href=\"http://www.focus.de/politik/deutschland/wowereits-berlin-slogan_nid_37712.html\" title=\"http://www.focus.de/politik/deutschland/wowereits-berlin-slogan_nid_37712.html\">\"poor but sexy\"</a>, then von Beust is, well, neither.)</p>\n<p>On the national level, bad boy Roland Koch has been whipped by Angela Merkel so much he's now a girlie man (so much so that the SPD is sending a <a href=\"http://www.spiegel.de/politik/deutschland/0,1518,452154,00.html\" title=\"Kochs Herausfordererin: Ségolène Ypsilanti - Politik - SPIEGEL ONLINE - Nachrichten\">girlie girl</a> against him in 2008). When the times of the grand coalition are such that the CDU spends an entire convention <a href=\"http://www.dresden2006.cdu.de/\">pretending to put on a human face</a>, it's no wonder that that disinterest in politics is at an all-time high.</p>\n<p>Hamburg needs you. Germany needs you. Ronald Schill, please come home!</p>"},"next":{"fields":{"title":"Do-It-Yourself Advent Calendar","date":"03 Dec 2006","slug":"/2006-12-03-do-it-yourself-advent-calendar/"}},"previous":{"fields":{"title":"Job Wanted","date":"05 Dec 2006","slug":"/2006-12-05-job-wanted/"}}},{"node":{"fields":{"title":"Do-It-Yourself Advent Calendar","date":"03 Dec 2006","slug":"/2006-12-03-do-it-yourself-advent-calendar/","tags":"Y2006"},"html":"<p>My boss had some problems <a href=\"http://sibylle.blogg.de/eintrag.php?id=432\">creating a Advent calendar</a> for his daughter, but luckily <a href=\"http://greenhaddock2.blogspot.com/2006/12/1st-advent.html\">Green Haddock has a calendar</a> that anyone (even men) can put together. It's so beautiful, though, you probably won't want to give it away. Happy Advent!</p>\n<p><img src=\"http://www.papascott.de/wordpress/wp-content/uploads/2006/12/bitburger_advent_calendar.jpg\" alt=\"Bitburger Advent Calendar\" title=\"Bitburger Advent Calendar\" /></p>\n<p>Tip: if you use the 30-pack of Astra Urtyp, you can enjoy your calendar for nearly a week longer!</p>"},"next":{"fields":{"title":"Christmas Market Monday","date":"03 Dec 2006","slug":"/2006-12-03-christmas-market-monday/"}},"previous":{"fields":{"title":"Ronald Schill, please come home!","date":"03 Dec 2006","slug":"/2006-12-03-ronald-schill-please-come-home/"}}}],"pathPrefix":"","first":false,"last":false,"index":173,"pageCount":1101}}

